
/* POPUP BACKGROUND */
.popup-overlay1 {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: rgba(0,0,0,0.4); */
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 40px;
  z-index: 9999;
}

.popup-box {
  margin-top: 80px;
  width: 95%;
  height: 92%;
  max-height: 95vh;
  /* background: #fff; */
  border-radius: 12px;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* CLOSE BUTTON */
.popup-close {
  position: absolute;
  top: 18px;
  right: 25px;
  font-size: 28px;
  cursor: pointer;
  color: black;
  z-index: 1000;
}
/* MAIN CONTENT AREA */
.popup-content {
  display: flex;
  height: 100%;
}
/* LEFT SIDE MENU */
.popup-sidebar {
  width: 220px;
  padding-top: 20px;
  border-right: 1px solid rgba(255, 255, 255, 0.3);
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.2);
  /* Slightly more opaque for sidebar */
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  /* Liquid transparent */
  display: flex;
  flex-direction: column;
}
.popup-sidebar-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  padding: 18px 25px;
  /* Taller items */
  width: 100%;
  transition: all 0.2s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 15px;
  font-weight: 500;
  color: #1a1a1a;
}
.popup-sidebar-item:hover {
  background: rgba(255, 255, 255, 0.3);
}
.popup-sidebar-item.active {
  background: rgba(255, 255, 255, 0.6);
  font-weight: 700;
  color: #000;
  border-left: 4px solid #01BF64;
  /* Active indicator */
}
/* DELETED ICON STYLES AS REQUESTED */
.popup-sidebar-text {
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  /* inherit from parent */
  text-align: left;
}
/* RIGHT SIDE PRODUCTS */
.popup-right {
  flex: 1;
  padding: 30px;
  overflow-y: auto;
  background: transparent;
  /* Transparent to show main background */
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}
.popup-title {
  font-size: 26px;
  margin-bottom: 25px;
  font-weight: 800;
  color: #1a1a1a;
  font-family: 'Inter', sans-serif;
}
/* PRODUCTS GRID */
.popup-grid {
  display: grid;
  /* grid-template-columns: repeat(auto-fill, minmax(260px, 1fr)); */
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}
.popup-card {
  position: relative;
  background: #fff;
  border: none;
  border-radius: 12px;
  padding: 16px;
  text-align: left;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  transition: transform 0.2s;
  height: 100%;
  justify-content: space-between;
}
.popup-card:hover {
  transform: translateY(-2px);
}
.popup-prod-img {
  width: 100%;
  height: 180px;
  object-fit: contain;
  margin-bottom: 10px;
}
.popup-location {
  background: #444;
  color: #fff;
  padding: 4px 8px;
  margin: 8px 0;
  font-size: 12px;
  /* align-self: flex-start; */
   width: 100%;  
  border-radius: 2px;
}
.popup-name {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 6px;
  color: #222;
  line-height: 1.3;
   min-height: 48px; 
}
.popup-weight {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 10px;
  font-size: 14px;
}
.popup-variant-select {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 10px;
  font-size: 14px;
  background-color: #fff;
  cursor: pointer;
  transition: border-color 0.2s;
}
/* .popup-variant-select:hover {
  border-color: #01BF64;
} */
.popup-variant-select:focus {
  outline: none;
  border-color: #000;
  box-shadow: 0 0 0 2px rgba(1, 191, 100, 0.1);
}
.popup-price {
  margin-top: auto;
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 800;
  color: #1a1a1a;
}
.popup-order-btn {
  width: 100%;
  padding: 12px;
  background: #01BF64;
  color: white;
  font-size: 16px;
  font-weight: 600;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  margin-top: auto;
}
.popup-order-btn:hover {
  background: #019f52;
}
/* BOTTOM FIXED LOGIN BAR */
.popup-bottom-login {
  width: 100%;
  background: #01BF64;
  padding: 18px;
  text-align: center;
  font-size: 22px;
  color: white;
  font-weight: bold;
  position: absolute;
  bottom: 0;
}

/* hide scrollbar for popup content */
.popup-right::-webkit-scrollbar,
.popup-sidebar::-webkit-scrollbar {
  width: 0px;
}
.popup-right,
.popup-sidebar {
  -ms-overflow-style: none;
  /* IE, Edge */
  scrollbar-width: none;
  /* Firefox */
}
/* RIBBON */
.product-ribbon {
  position: absolute;
  top: 10px;
  left: 0;
  background-color: #01BF64;
  color: white;
  padding: 4px 10px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  z-index: 5;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}
