/* DESKTOP  unchanged (Riverford style) */
.hero-features {
 margin-top: 0px !important;
  background: #f4f1ec;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 200px;
  padding: 50px 80px;
  text-align: center;
  padding-top: 40px !important;
}
body {
  background:#f4f1ec;
}


.feature-box img {
  width: 105px;
  margin-bottom: 5px;
}

.feature-box p {
  font-family: "Special Elite", system-ui;
  font-weight: 400;
  font-style: normal;
  font-size: 6px;
  margin: 0;
  letter-spacing: 0.5px;
}

/* Large Desktop/Monitor View */
@media (min-width: 1400px) and (max-width: 1799px) {
  .hero-features {
    gap: 250px;
    padding: 80px 120px; /* restore desktop spacing */
  }
  
  .feature-box img {
    width: 115px;
  }
  
  .feature-box p {
    font-size: 24px;
  }
}



/* -------------------------------------- */
/*  MOBILE  EXACT RIVERFORD LAYOUT     */
/* -------------------------------------- */
@media (max-width: 768px) {

  /* full mobile layout - wrap to create rows */
  .hero-features {
     margin-top: -190px !important;    
    padding-top: 10px !important;   
    padding-bottom: 40px;
    background: #f4f1ec;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0px 20px 60px;
    text-align: center;
    gap: 0px;
    width: 100%;
    
  }

  /* Reset all feature boxes */
  .hero-features .feature-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 0;
    box-sizing: border-box;
  }

  /* 100% ORGANIC (bee) - centered at top, full width */
  .hero-features .feature-box:nth-child(2) {
    order: 1;
    width: 100%;
    /* margin-bottom: 50px; */
    flex-basis: 100%;  
  }

  .hero-features .feature-box:nth-child(2) img {
    width: 120px;
     margin-bottom: 10px;   
  }

  .hero-features .feature-box:nth-child(2) p {
    font-size: 22px;
    font-weight: 525;
    line-height: 1.2;
  }

  /* Direct from farmers - left side of second row */
  .hero-features .feature-box:nth-child(1) {
    order: 2;
    width: 48%;
    flex-basis: 48%;
    padding: 0 10px;
    box-sizing: border-box;
    margin-top: 50px;
  }

  .hero-features .feature-box:nth-child(1) img {
    width: 70px;
    margin-bottom: 10px;
  }

  .hero-features .feature-box:nth-child(1) p {
    font-size: 16px;
    font-weight: 525;
    line-height: 1.2;
  }

  /* No artificial chemicals - right side of second row */
  .hero-features .feature-box:nth-child(3) {
    order: 3;
    width: 48%;
    flex-basis: 48%;
    padding: 0 10px;
    box-sizing: border-box;
    margin-top: 50px;
  }

  .hero-features .feature-box:nth-child(3) img {
    width: 70px;
    margin-bottom: 10px;
  }

  .hero-features .feature-box:nth-child(3) p {
    font-size: 16px;
    font-weight: 525;
    line-height: 1.2;
  }
}

@media (min-width: 1200px) {
  .hero-features {
    margin-top: -40px;          /* restore original desktop offset */
    background: #f4f1ec;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 250px;         /* restore original horizontal gap */
    padding: 80px 0;    /* restore original vertical spacing */
    text-align: center;
  }

  .feature-box img {
    width: 110px;
  }

  .feature-box p {
    font-size: 24px;
  }
}

