/* ---------- font (paste at top of your main CSS once) ---------- */

/* ---------- TABLET: 769px - 1023px (exclude iPad Pro) ---------- */
@media (min-width: 769px) and (max-width: 1023px) {

  /* ensure page can't scroll horizontally on tablet */
  html, body {
    overflow-x: hidden;
  }

  /* center the whole feature row and limit its visual width */
  .hero-features {
    margin-top: -290px !important;
    box-sizing: border-box;
    width: 100%;
    max-width: 860px;        /* visual content width similar to Riverford tablet */
    margin: 0 auto;          /* center on page */
    padding: 8px 10px;      /* breathing space above/below */
    background: #f4f1ec;
    display: flex;
    justify-content: center; /* center the 3 items */
    align-items: flex-start; /* keeps icons aligned to top of each box */
    gap: 30px;              /* spacing between boxes (tweak +/- 10px if needed) */
    text-align: center;
    overflow: hidden;        /* stop any child overflow */
  }


  /* make each feature box same size and centered */
  .hero-features .feature-box {
    width: 200px;            /* fixes width for consistent layout */
    min-width: 140px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    margin: 0;               /* remove mobile offsets */
    order: 0;               /* cancel any mobile reordering */
  }

  /* icon size tuned for tablet */
  .hero-features .feature-box img {
    width: 76px;             /* Riverford-like icon size on tablet */
    height: auto;
    display: block;
    margin-bottom: 10px;
  }

  /* heading font: Caveat to keep same handwritten look */
  .hero-features .feature-box p {
    font-family: "Special Elite", system-ui;
  font-weight: 400;
  font-style: normal;
    font-size: 20px;         /* large but readable on tablet */
    line-height: 1.05;
    margin: 0;
    color: inherit;
    letter-spacing: 0.5px;
  }

  /* If you used nth-child mobile ordering, reset spacing for tablet */
  .hero-features .feature-box:nth-child(1),
  .hero-features .feature-box:nth-child(2),
  .hero-features .feature-box:nth-child(3) {
    margin-top: 0;
  }
}

/* iPad Air: pull icon row closer to the green hero */
@media (min-width: 820px) and (max-width: 1023px) {
  .hero-features {
    margin-top: -300px !important;   /* move icons upward (icons section only) */
    padding-top: 0 !important;      /* remove extra space above icons */
    padding-bottom: 36px;           /* slightly tighter space below */
  }
}

/* ===================================== */
/* iPad Pro  reduce large top gap ONLY  */
/* 10241366 (portrait) & 13661024      */
/* ===================================== */
@media (width: 1024px) and (orientation: portrait),
       (width: 1366px) and (orientation: landscape) {

  .hero-features {
